import { createMuiTheme } from 'material-ui/styles';
import blue from 'material-ui/colors/blue';
import orange from 'material-ui/colors/orange';

const theme = createMuiTheme({
    palette: {
        primary: blue,
        secondary: orange,
    },
});

export default theme;



